'use client';

import { Pencil, Trash2, Loader2, Search, ChevronLeft, ChevronRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge'; // Pastikan sudah install badge
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import { StatusBooking } from '@/types';

interface StatusBookingTableProps {
    loading: boolean;
    statuses: StatusBooking[];
    totalData: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (page: number) => void;
    onEdit: (item: StatusBooking) => void;
    onDelete: (id: number) => void;
}

export default function StatusBookingTable({
    loading,
    statuses,
    totalData,
    currentPage,
    itemsPerPage,
    onPageChange,
    onEdit,
    onDelete
}: StatusBookingTableProps) {
    
    const totalPages = Math.ceil(totalData / itemsPerPage);
    const startNumber = (currentPage - 1) * itemsPerPage + 1;

    const getBadgeColor = (name: string) => {
        const n = name.toLowerCase();
        if (n.includes('menunggu') || n.includes('pending')) return 'bg-yellow-100 text-yellow-800 hover:bg-yellow-100';
        if (n.includes('konfirmasi')) return 'bg-blue-100 text-blue-800 hover:bg-blue-100';
        if (n.includes('selesai') || n.includes('lunas') || n.includes('terkonfirmasi')) return 'bg-green-100 text-green-800 hover:bg-green-100';
        if (n.includes('batal') || n.includes('tolak')) return 'bg-red-100 text-red-800 hover:bg-red-100';
        return 'bg-gray-100 text-gray-800';
    }

    return (
        <div className="rounded-md border bg-white shadow-sm">
            <Table>
                <TableHeader>
                    <TableRow className="bg-gray-50">
                        <TableHead className="w-[50px] text-center">No</TableHead>
                        <TableHead>Nama Status</TableHead>
                        <TableHead>ID Sistem</TableHead>
                        <TableHead className="text-right">Aksi</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {loading ? (
                        <TableRow>
                            <TableCell colSpan={4} className="h-24 text-center text-gray-500">
                                <Loader2 className="h-5 w-5 animate-spin inline mr-2" /> Memuat...
                            </TableCell>
                        </TableRow>
                    ) : statuses.length === 0 ? (
                        <TableRow>
                            <TableCell colSpan={4} className="h-32 text-center text-gray-500">
                                <div className="flex flex-col items-center justify-center">
                                    <Search className="h-8 w-8 text-gray-300 mb-2" />
                                    <p>Tidak ada data status.</p>
                                </div>
                            </TableCell>
                        </TableRow>
                    ) : (
                        statuses.map((item, index) => (
                            <TableRow key={item.id} className="hover:bg-gray-50">
                                <TableCell className="text-center text-gray-500">
                                    {startNumber + index}
                                </TableCell>
                                <TableCell>
                                    <Badge className={`${getBadgeColor(item.nama_status)} border-none shadow-none font-medium`}>
                                        {item.nama_status}
                                    </Badge>
                                </TableCell>
                                <TableCell className="text-xs text-gray-400 font-mono">
                                    ID: {item.id}
                                </TableCell>
                                <TableCell className="text-right space-x-2">
                                    <Button variant="ghost" size="icon" onClick={() => onEdit(item)}>
                                        <Pencil className="h-4 w-4 text-blue-600" />
                                    </Button>
                                    <Button 
                                        variant="ghost" 
                                        size="icon" 
                                        onClick={() => onDelete(item.id)}
                                        disabled={item.id <= 4} 
                                        title={item.id <= 4 ? "Status bawaan sistem tidak bisa dihapus" : "Hapus Status"}
                                        className={item.id <= 4 ? "opacity-30" : ""}
                                    >
                                        <Trash2 className="h-4 w-4 text-red-600" />
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))
                    )}
                </TableBody>
            </Table>

            {/* Pagination */}
            {!loading && totalData > 0 && (
                <div className="flex items-center justify-between p-4 border-t">
                    <div className="text-sm text-gray-500">
                         Total: {totalData} Status
                    </div>
                    <div className="flex items-center space-x-2">
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage - 1)} disabled={currentPage === 1}>
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-sm font-medium px-2">Hal {currentPage} / {totalPages}</span>
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage + 1)} disabled={currentPage === totalPages}>
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    </div>
                </div>
            )}
        </div>
    );
}