'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { StatusBooking } from '@/types';

import StatusBookingFilters from '@/components/admin/status-booking/StatusBookingFilters';
import StatusBookingTable from '@/components/admin/status-booking/StatusBookingTable';
import StatusBookingFormDialog from '@/components/admin/status-booking/StatusBookingFormDialog';

export default function StatusBookingPage() {
    // State
    const [statuses, setStatuses] = useState<StatusBooking[]>([]);
    const [loading, setLoading] = useState(true);
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [editData, setEditData] = useState<StatusBooking | null>(null);

    // Filter & Pagination
    const [searchQuery, setSearchQuery] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    // Fetch Data
    const fetchStatuses = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/statusBookings');
            setStatuses(response.data);
        } catch (error) {
            toast.error("Gagal memuat data status.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchStatuses(); }, []);

    // Filter Logic
    const filteredStatuses = statuses.filter((item) => 
        item.nama_status.toLowerCase().includes(searchQuery.toLowerCase())
    );

    // Pagination Logic
    useEffect(() => { setCurrentPage(1); }, [searchQuery]);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredStatuses.slice(indexOfFirstItem, indexOfLastItem);

    // Handlers
    const handleCreate = () => {
        setEditData(null);
        setIsDialogOpen(true);
    };

    const handleEdit = (item: StatusBooking) => {
        setEditData(item);
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin hapus status ini?")) return;
        try {
            await axios.delete(`/api/statusBookings/${id}`);
            toast.success("Status dihapus");
            fetchStatuses();
        } catch {
            toast.error("Gagal menghapus (Mungkin sedang digunakan).");
        }
    };

    const handleSubmit = async (formData: any, isEdit: boolean) => {
        try {
            if (isEdit && editData) {
                // Update
                await axios.put(`/api/statusBookings/${editData.id}`, formData);
                toast.success("Status diperbarui!");
            } else {
                // Create
                await axios.post('/api/statusBookings', formData);
                toast.success("Status ditambahkan!");
            }
            setIsDialogOpen(false);
            fetchStatuses();
        } catch (error: any) {
            console.error(error);
            const message = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(message);
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Status Booking</h1>
                    <p className="text-gray-500 text-sm">Kelola status alur kerja booking (Pending, Lunas, dll).</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={handleCreate}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah Status
                </Button>
            </div>

            <StatusBookingFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

            <StatusBookingTable 
                loading={loading}
                statuses={currentItems}
                totalData={filteredStatuses.length}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleEdit}
                onDelete={handleDelete}
            />

            <StatusBookingFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                initialData={editData}
            />
        </div>
    );
}